/* src/App.css */
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
  }
  
  .sign-up-container {
    margin: auto;
    margin-top: 100px;
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 300px;
  }
  
  .sign-up-container h2 {
    margin-bottom: 20px;
  }
  
  .sign-up-container form div {
    margin-bottom: 15px;
  }
  
  .sign-up-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .sign-up-container input {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
  }
  
  .sign-up-container .error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
  }
  
  .sign-up-container button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 40px;
    cursor: pointer;
  }
  
  .sign-up-container button:hover {
    background-color: #0056b3;
  }
  
  /* Style for the login redirect */
  .login-redirect {
    margin-top: 20px;
    text-align: center;
  }
  
  .login-redirect p {
    margin: 0;
  }
  
  .login-redirect a {
    color: #007bff;
    text-decoration: none;
  }
  
  .login-redirect a:hover {
    color: #0056b3;
  }

  